import React, { Component } from "react"
class CountryList extends Component {
  state = {
    countryName: ""
  }
  renderCountry = each => {
    return (
      <li
        className="items"
        key={each.currency}
        onClick={() => this.props.handleClick(each.currency)}
      >
        {each.country}
      </li>
    )
  }
  handleChange = e => {
    this.setState({
      countryName: e.target.value
    })
  }
  render() {
    return (
      <>
        <input
          type="text"
          value={this.state.countryName}
          onChange={this.handleChange}
        />
        <ul>{this.props.countriesthis.props.countries.map(each => this.renderCountry(each))}</ul>
      </>
    )
  }
}
export default CountryList
